/* Container of the switch */
.toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 30px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  border-radius: 7px;
}

.slider:before {
  box-sizing: border-box;
  position: absolute;
  content: "";
  height: 30px;
  width: 30px;
  background-color: white;
  transition: 0.4s;
  border-radius: 7px;
  border: 1px solid #ccc;
}

input:checked + .slider {
  background-color: var(--board-color);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--board-color);
}

input:checked + .slider:before {
  transform: translateX(30px);
}

.toggle-day {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
}/*# sourceMappingURL=Toggle.css.map */